import React, { useEffect, useState } from 'react'
import {
    Card, Row, Col, Button, Space, Input, Table, Modal, message, Select
} from 'antd';

import handleApiError from '../../../helpers/handleApiError';
import hasAuthObject from '../../../helpers/hasAuthObject';
import Api from '../../../helpers/api';

import {
    PlusOutlined,
    EditOutlined,
    DeleteOutlined,
} from '@ant-design/icons';

const { Option } = Select;

const authobjectCfg = {
    view: 'role-view', //button display detail    
    create: 'role-create', //button add
    update: 'role-update', //button edit
    delete: 'role-delete', //button delete
    print: 'role-print', //button print
}

const api = new Api;

const List = (props) => {

    const [selectedRowKeys, setSelectedRowKeys] = useState([])
    const [loading, setLoading] = useState(false)
    const [q, setQ] = useState('')
    const [data, setData] = useState([])
    const [pagination, setPagination] = useState({})

    useEffect(() => {
        fetch()
    }, [q])

    const fetch = (params = {}) => {
        // console.log('params:', params);
        setLoading(true)
        api.getMasterRoleList({ ...params, q })
            .then(response => {
                if (response.status === 200) { //OK
                    const data = response.data;

                    let pagination = {}

                    pagination.current = data.current_page
                    pagination.total = data.total;
                    pagination.pageSize = parseInt(data.per_page);

                    setData(data.data)
                    setPagination(pagination)
                    setLoading(false)
                }
            })
            .catch(error => {
                setLoading(false)
                handleApiError(error, props)
            })

    }

    const handleTableChange = (paginate, filters, sorter) => {

        const pager = { ...pagination };
        pager.current = paginate.current;

        setPagination(pager)

        fetch({
            per_page: paginate.pageSize,
            page: paginate.current,
            sort_field: sorter.field,
            sort_order: sorter.order === 'ascend' ? 'asc' : 'desc',
            // ...filters,
        });
    }

    const handleEdit = () => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);

        if (selectedRowKeys.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowKeys.length} data, silahkan hanya pilih 1 data`,
            });
        } else {
            props.history.push(`/role/edit/${selectedRowKeys[0]}`)
        }
    }

    const handleDelete = () => {
        Modal.confirm({
            title: `Anda yakin akan menghapus ${selectedRowKeys.length} data?`,
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            onOk: () => {
                massDeleteData()
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const massDeleteData = () => {
        api.massDeleteRole({ id: selectedRowKeys })
            .then(response => {
                // console.log(response.data)
                message.success('Delete data success');
                fetch();
                setSelectedRowKeys([])
            })
            .catch(error => {
                //console.log(error.response)
                message.error('Failed to delete');
                handleApiError(error, props)
            });
    }

    //on check table item
    const onSelectChange = (selectedRowKeys) => {
        setSelectedRowKeys(selectedRowKeys)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
        // type: 'radio'
    };

    const searchData = (value) => {
        setQ(value);
    }

    const hasSelected = selectedRowKeys.length > 0;

    const columns = [
        {
            title: 'Role',
            dataIndex: 'name',
            sorter: true,
            width: 100
        },
        {
            title: 'Total Menu Object',
            dataIndex: 'total_auth_object',
            width: 80,
            sorter: true,
            className: 'col-number',
        },
        {
            title: 'Total User',
            dataIndex: 'total_user',
            width: 80,
            sorter: true,
            className: 'col-number',
        },
    ]

    return (
        <div>
            <Card>
                <Space direction="vertical" size="large" style={{ width: '100%' }}>
                    <Row>
                        <Col md={19}>
                            <Space>
                                {hasAuthObject(authobjectCfg.create) &&
                                    <Button
                                        type="primary"
                                        onClick={() => { props.history.push(`${props.location.pathname.split('/').slice(0, -1).join('/')}/add`) }}
                                        icon={<PlusOutlined />}
                                    >Add</Button>
                                }

                                {hasAuthObject(authobjectCfg.update) &&
                                    <Button
                                        onClick={() => handleEdit()}
                                        disabled={!hasSelected}
                                        icon={<EditOutlined />}
                                    >Edit</Button>
                                }

                                {hasAuthObject(authobjectCfg.delete) &&
                                    <Button
                                        danger
                                        onClick={() => handleDelete()}
                                        disabled={!hasSelected}
                                        icon={<DeleteOutlined />}
                                    >Delete</Button>
                                }
                            </Space>
                        </Col>

                        <Col md={5} >
                            <Input.Search placeholder="Search"
                                onSearch={searchData} />
                        </Col>
                    </Row>
                    <Row>
                        <Col lg={24} xs={24}>
                            <Table
                                loading={loading}
                                rowSelection={hasAuthObject(authobjectCfg.view || authobjectCfg.update || authobjectCfg.delete) ? rowSelection : null}
                                columns={columns}
                                rowKey={record => record.id}
                                onChange={handleTableChange}
                                dataSource={data}
                                pagination={{
                                    pageSizeOptions: ['10', '50', '100', '200'],
                                    showSizeChanger: true,
                                    showQuickJumper: true,
                                    showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
                                    ...pagination
                                }}
                            />
                        </Col>
                    </Row>
                </Space>
            </Card>
        </div>
    )
}

export default List